<template>
  <div class="formup">
    <h1>Cadastro de Produtos</h1>
      <form class="createform" @submit.prevent="addNewProduct">
    	        <input v-model="newProduct.codigoDeBarra" type="text" id="codigoDeBarra" name="codigoDeBarra" placeholder="Código de Barra" required="required" /><br>
              <input v-model="newProduct.nome" type="text" id="nome" name="nome" placeholder="Descrição do produto" required="required" /><br>
                <input v-model="newProduct.estoque" type="text" id="estoque" name="estoque" placeholder="Quantidade" required="required" /><br>
                <input v-model="newProduct.valorUn" type="text" id="valorUn" name="valorUn" placeholder="Valor unitário"/> <br>
                <button type="button" class="btn btn-primary btn-block btn-large">Enviar</button>
      </form>
  </div>
</template>

<script>
export default {
  name: 'CreateProducts',

  data(){
      return{
          produto:{}
      }
  },

  created: function() {
      this.$http.get("http://localhost:8080/produto").then(function(response){
          this.produtos = response.body;
      }, function(response){
          console.log("nao funcionou")
      })
  }
  }


</script>

<style scoped>
  .formup{
    margin: 13% auto 0 auto;
  }

  input{
    width: 40%;
  }

  button{
    width: 15%;
    margin: 0 auto;
  }
</style>


